// This is a simulated AI age detection function for demonstration purposes
// In a production environment, this would be replaced with a real AI service

export async function analyzeImageForAge(imageDataURL: string): Promise<number> {
  // Simulate processing delay
  await new Promise(resolve => setTimeout(resolve, 2000));

  try {
    // For demo purposes, we'll generate a random age between 13 and 20
    // In production, this would be replaced with actual AI image analysis
    const simulatedAge = Math.floor(Math.random() * 8) + 13; // Returns age between 13-20

    // Simulate some randomness in the age detection to make it more realistic
    const variance = Math.random() * 2 - 1; // Random variance between -1 and 1
    const finalAge = Math.round(simulatedAge + variance);

    // Log the simulated age detection (for development purposes)
    console.log(`Simulated age detection: ${finalAge} years old`);

    return finalAge;
  } catch (error) {
    console.error("Error in age detection:", error);
    throw new Error("Failed to analyze image for age detection");
  }
}

// Helper function to validate if the detected age is within acceptable range
export function isAgeValid(age: number): boolean {
  return age >= 13 && age <= 17;
}
